import React from 'react';
// Importa o React para criar componentes.

import { ScatterChart, Scatter, XAxis, YAxis, ZAxis, Tooltip, ResponsiveContainer } from 'recharts';
// Importa componentes especficos do Recharts para construir o grfico de disperso.

import { translations } from './translation';
// Importa as tradues do arquivo de tradues.

export const calculateWeightedScores = (alternatives, criteria) => {
  // Funo para calcular as pontuaes ponderadas de cada alternativa com base nos critrios.

  return alternatives.map(alt => ({
    name: alt.name,
    // Nome da alternativa.

    score: alt.scores.reduce((acc, score, index) => acc + score * criteria[index].weight, 0) / criteria.reduce((acc, crit) => acc + crit.weight, 0)
    // Calcula a pontuao ponderada somando os produtos das pontuaes e pesos dos critrios,
    // e depois dividindo pelo somatrio dos pesos dos critrios.
  }));
};

const CustomTooltip = ({ active, payload, language }) => {
  // Componente para personalizar o tooltip (dica) que aparece quando o usurio passa o mouse sobre um ponto no grfico.

  if (active && payload && payload.length) {
    const data = payload[0].payload;
    // Obtm os dados do ponto sobre o qual o mouse est passando.

    const t = translations[language];
    // Usa as tradues baseadas no idioma selecionado.

    return (
      <div className="custom-tooltip" style={{ backgroundColor: '#1a1a1a', padding: '10px', border: '1px solid #00ff9d' }}>
        {/* Tooltip personalizado com fundo escuro e borda verde */}
        <p className="label" style={{ color: '#00ff9d' }}>{`${t.name}: ${data.name}`}</p>
        <p style={{ color: '#00ff9d' }}>{`${t.score}: ${data.x.toFixed(2)}`}</p>
        <p style={{ color: '#00ff9d' }}>{`${t.avgCriterionWeight}: ${data.y.toFixed(2)}`}</p>
        {/* Exibe o nome, pontuao e peso mdio do critrio da alternativa */}
      </div>
    );
  }
  return null;
  // Retorna null se o tooltip no estiver ativo.
};

export const ImprovedScatterChart = ({ alternatives, criteria, calculateWeightedScores, language }) => {
  // Componente que renderiza um grfico de disperso personalizado.

  const t = translations[language];
  // Usa as tradues baseadas no idioma selecionado.

  const data = alternatives.map((alt, index) => {
    const weightedScore = calculateWeightedScores(alternatives, criteria)[index].score;
    // Calcula a pontuao ponderada para cada alternativa.

    return {
      x: weightedScore,
      // Define a posio no eixo X como a pontuao ponderada.

      y: criteria.reduce((acc, criterion) => acc + criterion.weight * alt.scores[criteria.indexOf(criterion)], 0) / criteria.length,
      // Calcula a posio no eixo Y como a mdia ponderada dos pesos dos critrios.

      z: weightedScore * 200,
      // Define o tamanho do ponto no grfico (eixo Z) com base na pontuao ponderada.

      name: alt.name
      // Define o nome da alternativa.
    };
  });

  return (
    <div className="h-80 w-full relative">
      {/* Continer para o grfico com altura e largura ajustveis */}
      <div className="absolute inset-0 bg-[url('data:image/svg+xml;base64,')] opacity-20" />
      {/* Fundo decorativo com padro SVG */}
      <ResponsiveContainer>
        {/* Componente que torna o grfico responsivo */}
        <ScatterChart margin={{ top: 20, right: 20, bottom: 60, left: 20 }}>
          {/* Grfico de disperso com margens definidas */}
          <XAxis
            type="number"
            dataKey="x"
            name="score"
            unit=""
            stroke="#00ff9d"
            tickLine={false}
            axisLine={false}
            label={{ value: t.weightedScore, position: 'bottom', fill: '#00ff9d' }}
            // Configuraes do eixo X, incluindo o rtulo e a cor do eixo.
          />
          <YAxis
            type="number"
            dataKey="y"
            name="weight"
            unit=""
            stroke="#00ff9d"
            tickLine={false}
            axisLine={false}
            label={{ value: t.avgCriterionWeight, angle: -90, position: 'left', fill: '#00ff9d' }}
            // Configuraes do eixo Y, incluindo o rtulo e a cor do eixo.
          />
          <ZAxis type="number" dataKey="z" range={[100, 1000]} name="score" unit="" />
          {/* Configuraes do eixo Z, que define o tamanho dos pontos no grfico */}
          <Tooltip content={<CustomTooltip language={language} />} />
          {/* Tooltip personalizado que exibe informaes adicionais ao passar o mouse sobre os pontos */}
          <Scatter
            data={data}
            fill="#00ff9d"
            shape={(props) => {
              const { cx, cy, fill, payload } = props;
              const size = (payload.z / 200) * 5;
              return (
                <g>
                  <circle cx={cx} cy={cy} r={size} fill={fill} fillOpacity={0.6} />
                  <circle cx={cx} cy={cy} r={size} fill="none" stroke={fill} strokeWidth={2} />
                  <circle cx={cx} cy={cy} r={size * 1.5} fill="none" stroke={fill} strokeWidth={1} opacity={0.5}>
                    <animate attributeName="r" from={size} to={size * 1.5} dur="1.5s" repeatCount="indefinite" />
                    <animate attributeName="opacity" from="0.8" to="0" dur="1.5s" repeatCount="indefinite" />
                  </circle>
                  <text x={cx} y={cy - size - 5} textAnchor="middle" fill="#00ff9d" fontSize="10">
                    {payload.name}
                  </text>
                </g>
              );
            }}
          />
          {/* Configura os pontos no grfico, com animaes e labels personalizados */}
        </ScatterChart>
      </ResponsiveContainer>
    </div>
  );
};
