/*
  Este arquivo contm utilitrios e dados de traduo que suportam o funcionamento do aplicativo de construo de diagramas de fluxo de processo.
  
  Funcionalidades:
  - **flattenProcesses**: Esta funo  utilizada para "achatar" uma estrutura de processos hierrquicos, transformando-a em um array simples, que contm todos os processos, incluindo aqueles que esto aninhados.
  - **getEligibleParents**: Esta funo identifica quais processos em um determinado nvel hierrquico podem ser considerados "pais" para novos processos que esto sendo adicionados.
  - **translations**: Este objeto contm as tradues para os textos usados na interface do aplicativo, suportando ingls (en) e portugus (pt). Voc pode adicionar ou modificar tradues conforme necessrio.

  Este arquivo  importante para manipular a estrutura hierrquica dos processos e para suportar a internacionalizao do aplicativo.
*/

export const flattenProcesses = (processes) => {
    // A funo flattenProcesses recebe um array de processos e retorna um array "achatado"
    return processes.reduce((acc, process) => {
      // Adiciona o processo atual ao array acumulador
      acc.push(process);
      // Se o processo atual tiver filhos, chama recursivamente flattenProcesses para achatar os filhos
      if (process.children) {
        acc.push(...flattenProcesses(process.children));
      }
      // Retorna o array acumulador atualizado
      return acc;
    }, []); // Inicializa o array acumulador como vazio
  };
  
  export const getEligibleParents = (processes, currentLevel) => {
    // A funo getEligibleParents retorna os processos que podem ser pais no nvel atual
    const currentLevelInt = parseInt(currentLevel); // Converte o nvel atual de string para nmero inteiro
    // Usa a funo flattenProcesses para obter todos os processos em um nico array e filtra para encontrar os pais elegveis
    return flattenProcesses(processes).filter(p => {
      const parentLevel = parseInt(p.level); // Converte o nvel do processo pai para nmero inteiro
      // Retorna true se o nvel do processo pai for exatamente um nvel acima do nvel atual
      return parentLevel === currentLevelInt - 1;
    });
  };
  
